@import url(https://fonts.googleapis.com/css2?family=Rakkas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Scheherazade:wght@400;700&display=swap);
body {
  direction: rtl;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color: #dc3545;
}
.App {
  background-color: #f8f9fa;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
}
h1, h2, h3, h4, h5, h6, .navbar-brand  {
  font-family: 'Rakkas', cursive;
  text-align: right;
}
p, ul, li {
  font-family: 'Scheherazade', serif;
  font-weight: bolder;
  font-size: large;
}
a.nav-link {
  padding: 3px;
}
div.col.title {
  padding-right: 1.5rem;
}
.title p {
  text-align: right;
  line-height: 2.35rem;
  font-size: 28px;
}
.navbar-brand {
  font-size: larger;
  letter-spacing: -1px;
  padding-top: 0.7rem;
}
span.line {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-color: #dc3545;
          text-decoration-color: #dc3545;
  -webkit-text-decoration-color: var(--color);
          text-decoration-color: var(--color);
}
.navbar-nav {
  margin-right: auto;
}
.image-title {
  text-align: center;
}
a.restlink {
  color: #000;
  color: initial;
  font-size: inherit !important;
}
p {
  color: #495057;
}
@media (max-width: 768px) { 
  img.img-fluid {
    max-width: 165px;
    height: 340px;
    object-position: 55% 50%;
    object-fit: cover;
  }
  .title h1 {
    font-size: 1.2rem;
  }
  .title p {
    font-size: 1.2rem;
    line-height: 1.6rem;
  }
  .title {
    padding-left: 0
  }
  
 }

nav {
    border-radius: 0px 0px 30px 0px;
}
a:not(.navbar-brand){
    font-family: 'Scheherazade', serif;
    font-weight: bolder;
    font-size: large;
  }
  a.nav-link {
    padding: 3px;
  }
  .navbar-brand {
    font-size: larger;
    letter-spacing: -1px;
    padding: 0.5rem;
  }
  .navbar-nav {
    margin-right: auto;
  }
.bd-red-500{
    background-color: var(--color);
}
.navbar-toggler:focus {
    text-decoration: none;
    text-decoration: initial; 
    outline: medium none invert; 
    outline: initial;
    box-shadow: none;
    box-shadow: initial;
}
.navbar-toggler {
    border: medium none currentColor;
    border: initial;
}
@media (min-width: 992px){
  a.nav-link {
    padding-right: 1.6rem !important;
  }
}
@media (max-width: 768px) { 
  .navbar-brand {
    padding-right: 0px;
    margin: 0.5px;
  }
}
h1 .trailer {
    padding: .5rem;
}
.w-custom {
    margin: auto;
    max-width: 85%;
    }
